#SHAPE1061{
     width: 45.7735px;
    height: 52.1577px;
    top: 86px;
    left: 263.897px;
     margin: 1rem;
}

.animationText{
     text-align: center;
     font-weight: bold;
     color: brown;
     animation-name: shake;
     animation-duration: 2s;
     animation-timing-function: ease-in-out;
     animation-iteration-count: infinite;
}
.animationText h3{
     font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
     font-weight: bolder;
}
@keyframes shake {
     0%, 100% {
       transform: scale(1.1);
     }
     40%{
          transform: rotate(-15deg);
       transform: scale(0.9);
     }
     60% {
          transform: rotate(-15deg);
       transform: scale(0.9);
     }
   }
   
   
   
   